@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 500;
  margin: 0; }

input[type="time"]::-webkit-clear-button, input[type="time"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-clear-button,
input[type="date"]::-webkit-inner-spin-button {
  display: none; }

input[type="radio"] {
  margin: 0; }

.app-page {
  display: flex;
  justify-content: center; }

.ui-button {
  display: inline-block;
  padding: 13px 30px 9px;
  background-color: #d3d3d3;
  border: none;
  -webkit-box-shadow: 0 3px 0 0 #818181; }

.ui-pseudo-button {
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  outline: 0;
  color: #68a831;
  font-size: 14px;
  border-bottom: 1px solid #68a831;
  cursor: pointer; }

.payment-repeat-form {
  margin: 20px; }
  .payment-repeat-form__header {
    padding: 15px 30px;
    background-color: #f6f6f6; }
  .payment-repeat-form__body {
    padding: 15px 30px; }

.payment-repeat-frequency__option {
  margin-bottom: 5px; }

.payment-repeat-frequency__label {
  display: inline-block;
  width: 140px; }

.payment-repeat-frequency__dates-selector {
  position: relative; }

.ui-calendar {
  position: absolute;
  top: 25px;
  padding: 60px;
  text-align: center;
  border: 1px solid #c7c7c7;
  background-color: #ffffff; }

.ui-form-row {
  display: flex;
  margin-bottom: 30px; }
  .ui-form-row__title {
    width: 100px;
    color: #93929a;
    font-size: 16px; }

.ui-option input[type="radio"] {
  margin-right: 5px; }

.payment-repeat-quantity__options {
  display: flex;
  justify-content: space-between; }

.payment-repeat-quantity__option {
  margin-right: 20px; }
  .payment-repeat-quantity__option input[type="text"],
  .payment-repeat-quantity__option input[type="date"] {
    margin: 0 10px; }

.payment-repeat-quantity__limited-by-qty-field {
  width: 25px; }

.payment-repeat-quantity__limited-by-date-field {
  width: 110px; }

.payment-repeat-time__field {
  width: 47px; }

.payment-repeat-time__description {
  display: inline-block;
  margin-left: 20px;
  color: #93929a;
  font-weight: 500;
  font-style: italic; }

.payment-repeat-submit__date, .payment-repeat-submit__repeater {
  display: block;
  margin-bottom: 10px; }

.payment-repeat-submit__button {
  margin-top: 30px; }
  .payment-repeat-submit__button:hover {
    box-shadow: 0 3px 0 0 #4aabff;
    cursor: pointer; }
  .payment-repeat-submit__button:focus {
    outline: 0;
    box-shadow: 0 3px 0 0 #4aabff; }
  .payment-repeat-submit__button:active {
    box-shadow: none; }

